import { useCorn } from '@react-corn/core';
import { ButtonRow, Text } from '@react-corn/simple';
import { useState } from 'react';
const fields = 1000;
const initialItem = Object.fromEntries(new Array(fields).fill().map((_, i) => [`field-${i}`, `value-${i}`]));
export const StressTest = () => {
  const [item, setItem] = useState(initialItem);
  const {
    form,
    field,
    modified,
    onReset
  } = useCorn({
    item,
    onSubmit: newItem => {
      const accepted = window.confirm(`You submitted "${Object.values(newItem).join(', ')}"`);
      accepted && setItem(newItem);
    }
  });
  return <form {...form} style={{
    display: 'flex',
    flexWrap: 'wrap'
  }}>
      {new Array(fields).fill().map((_, i) => <Text key={i} required {...field(`field-${i}`)}>
          Field {i}
        </Text>)}
      <div style={{
      width: '100%'
    }} />
      <ButtonRow>
        <button disabled={!modified}>Submit</button>
        <button type="button" disabled={!modified} onClick={onReset}>
          Reset
        </button>
      </ButtonRow>
    </form>;
};
export default {
  title: 'Sandbox/simple-stress-test',
  parameters: {
    options: {
      showPanel: false
    }
  }
};
StressTest.parameters = {
  ...StressTest.parameters,
  docs: {
    ...StressTest.parameters?.docs,
    source: {
      originalSource: "() => {\n  const [item, setItem] = useState(initialItem);\n  const {\n    form,\n    field,\n    modified,\n    onReset\n  } = useCorn({\n    item,\n    onSubmit: newItem => {\n      const accepted = window.confirm(`You submitted \"${Object.values(newItem).join(', ')}\"`);\n      accepted && setItem(newItem);\n    }\n  });\n  return <form {...form} style={{\n    display: 'flex',\n    flexWrap: 'wrap'\n  }}>\n      {new Array(fields).fill().map((_, i) => <Text key={i} required {...field(`field-${i}`)}>\n          Field {i}\n        </Text>)}\n      <div style={{\n      width: '100%'\n    }} />\n      <ButtonRow>\n        <button disabled={!modified}>Submit</button>\n        <button type=\"button\" disabled={!modified} onClick={onReset}>\n          Reset\n        </button>\n      </ButtonRow>\n    </form>;\n}",
      ...StressTest.parameters?.docs?.source
    }
  }
};